// Page user account manager

.page-user-manager {
    padding: 40px 0 100px;
}

.um {
    &-general {
        padding: 50px;
    }

    &-top {
        margin-bottom: 54px;

        .list-inline-item {
            width: 429px;
            height: 87px;
            background: $light;
            line-height: 87px;
            text-align: center;

            a {
                color: $black;
                width: 100%;
                height: 100%;
                display: block;
            }

            &:hover,
            &.active {
                a {
                    background: $primary;
                    color: $white;
                }
            }
        }
    }

    &-links {
        li {
            width: 100%;
            height: 86px;
            background: $light;
            line-height: 87px;
            text-align: center;
            margin-bottom: 20px;

            a {
                color: $black;
                width: 100%;
                height: 100%;
                display: block;
            }

            &:hover,
            &.active {
                a {
                    background: $primary;
                    color: $white;
                }
            }
        }
    }

    &-right {
        background-color: $light;
    }
}

.general {
    &-top {
        position: relative;
        margin-bottom: 60px;
    }

    &-avatar {
        position: relative;
        height: 200px;
        width: 200px;
        margin: 0 auto;
        border-radius: 50%;
        overflow: hidden;
        transition: all .3s ease;
        box-shadow: 0 0 5px rgba($color: #000000, $alpha: .15);
    }

    &-icon {
        width: 50px;
        height: 50px;
        position: absolute;
        bottom: -20px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 2;
        background: $white;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
    }

    &-picture {
        height: 100%;
        width: 100%;
        transition: all .3s ease;
        object-fit: cover;
        border-radius: 50%;
    }

    &-upload-btn {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
    }

    &-file-upload {
        overflow: hidden;
        display: none;
        width: 0;
        height: 0;
        text-indent: -999px;
    }
}

.um-change-password {
    padding: 100px 0;

    .icon-show-pass {
        position: absolute;
        right: 32px;
        top: 40px;
        max-width: 24px;
        cursor: pointer;
    }
}

.um-reservation,
.um-coupon {
    padding: 30px;
}

.page-um-coupon-detail {
    .detail-remove {
        text-decoration: underline;
    }
}

.um-event-detail {
    &-right {
        background: $bg-secondary;
        padding: 40px 64px;
    }

    .horizontal-img {
        width: 100px;
        height: 100px;
        margin-right: 16px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .btn-join {
        width: 306px;
        margin-top: 16px;
    }

    .info {
        li {
            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
    }

    .detail-horizontal {
        img {
            box-shadow: 0px 2px 6px rgba(77, 78, 78, 0.2);
        }
    }
}

