video {
    width: 100%;
}

.form-group-datepicker {
    .form-control {
        padding-left: 60px;
        position: relative;
    }

    .datepicker-wraper {
        .icon-calendar {
            position: absolute;
            z-index: 2;
            top: 14px;
            left: 12px;
        }
    }
}

.form-group-timepicker {
    .timepicker-wraper {
        &::after {
            position: absolute;
            right: 12px;
            top: 26px;
            z-index: 2;
            content: '';
            background-image: url('../images/ic-dropdown.svg');
            background-repeat: no-repeat;
            background-position: center;
            width: 16px;
            height: 8px;
        }
    }
}

.form-control:focus,
.btn:focus,
.navbar-toggler:focus {
    box-shadow: none;
}

.form-control:focus {
    border-color: $text-color;
    box-shadow: none;
}