@media screen and (max-width: 992px) {
    body {
        font-size: 18px;
        line-height: 20px;
    }

    .page-title {
        font-size: $fs32;
        line-height: 32px;
    }

    .page-title-md {
        font-size: $fs28;
        line-height: 28px;
    }

    .page-title-sm {
        font-size: $fs24;
        line-height: 24px;
    }

    .page-title-xs {
        font-size: $fs20;
        line-height: 20px;
    }

    .page-text-lg {
        font-size: $fs20;
        line-height: 24px;
    }

    .page-text-md {
        font-size: $fs18;
        line-height: 20px;
    }

    .page-text-sm {
        font-size: $fs16;
        line-height: 18px;
    }

    .page-text-xs {
        font-size: $fs14;
        line-height: 16px;
    }

    .page-text-xxs {
        font-size: $fs14 - 2;
        line-height: 14px;
    }

    .mb-100 {
        margin-bottom: 50px;
    }

    .signin-right {
        margin: 100px 0;
    }

    .form-control-lg {
        min-height: 44px;
        padding: 9px 12px;
        font-size: 18px;
        line-height: 20px;
    }

    .form-check {
        &-input {
            width: 20px;
            height: 20px;
        }

        &-label {
            font-size: 16px;
            line-height: 18px;
            margin: 0;
        }
    }

    .form-label,
    .invalid-feedback {
        font-size: 16px;
        line-height: 18px;
    }

    .signup-img {
        img {
            width: 80%;
            height: auto;
        }
    }

    .customer-service-content {
        padding-left: 0;
    }
}

@media screen and (max-width: 768px) {
    .page-business-profile {
        padding: 40px 0;
    }

    .modal-header {
        padding-top: 30px !important;
        padding-right: 30px !important;
    }

    .modal-body {
        padding: 30px !important;
    }

    .customer-event {
        &-list {
            padding: 20px 0;
        }

        &-item {
            margin-bottom: 16px;
        }
    }

    .bp-top {
        .container {
            height: 200px;
        }
    }

    .bp-header {
        margin-top: -50px;

        img {
            width: 150px;
            height: 150px;

        }
    }

    .bp-tabs {
        margin-top: 24px;

        &-right {
            padding: 16px 16px 0;
            margin-top: 24px;

            .open-hour {
                padding: 16px 16px 0;
                margin-top: 24px;
            }
        }
    }

    .page-bp-coupon .bp-tabs-right {
        padding: 16px;
    }

    .bp-book-reservation {
        .container {
            padding: 20px 30px;
        }
    }

    .bm {
        &-create {
            &-content {
                padding: 40px 80px;
            }
        }
    }

    .customer-service-grid {
        padding: 50px 0;

        .customer-service-item {
            margin-bottom: 30px;

            .card-title {
                font-size: 20px;
                line-height: 34px;
                margin-bottom: 0;
            }
        }

        [class*='col'] {
            padding-left: 15px;
            padding-right: 15px;
        }

        .page-heading {
            margin-bottom: 30px;
            padding: 0;
        }
    }

    .customer-item-list {
        padding: 20px 32px;
    }

    .customer-coupon {
        padding: 60px 0;

        .search-box {
            margin: 30px 0;
        }
    }

    .grid-60 {
        .row {
            margin-left: -16px;
            margin-right: -16px;
        }

        [class*='col-'] {
            padding-left: 16px;
            padding-right: 16px;
        }
    }

    #aboutMapModal {
        iframe {
            height: 350px;
        }
    }

    .bp-top {
        margin-top: 35px;
    }

    .bp-sidebar {
        li {
            margin-bottom: 0;
        }

        a {
            padding: 16px;
            font-size: $fs20;
            line-height: 24px;
        }
    }

    .bp-introduce {
        margin-top: 35px;
    }

    .bp {
        &-event {
            .search-box {
                margin-bottom: 24px;
            }
        }

        &-account {
            .has-border {
                border-left: 0;
            }
        }

        &-join {
            &-form {
                background: $bg-secondary;
                padding: 16px 24px;

                h2 {
                    margin-bottom: 24px;
                }
            }
        }

        &-review {
            padding-left: 0;

            &-zero {
                margin-top: 30px;
            }

            .review-top {
                padding: 30px 0;
            }

            .comment-item {
                background-color: $bg-secondary;
                margin-bottom: 20px;
                padding: 20px;

                &.no-avatar {
                    width: calc(100% - 80px);
                }
            }

            .overall-rate {
                height: 200px;

                h5 {
                    margin-bottom: 20px;
                }
            }

            .comment-img {
                margin-right: 0;
                padding-top: 0;
                margin-bottom: 16px;
                display: flex;
                flex-direction: column;

                span {
                    text-align: left;
                }

                img {
                    width: 150px;
                    height: 150px;
                    object-fit: cover;
                    border-radius: 50%;
                }
            }

            .comment-body {
                img {
                    width: 200px;
                    height: 250px;
                    object-fit: cover;
                    margin-bottom: 20px;
                }
            }
        }

        &-reservation {
            .reservation-top {
                margin-bottom: 24px;
            }

            &-zero {
                .btn {
                    margin-top: 24px;
                }
            }

            th,
            td {
                min-width: 60px;
                padding-left: 16px;
                padding-right: 16px;
            }

            .reservation-table {
                .table {
                    margin-bottom: 0;
                }
            }
        }
    }

    .bp-gallery {
        .page-pagination {
            margin-top: 0;
        }

        .gallery-zero {
            height: 400px;
            padding-top: 100px;
        }
    }

    .nav-pills {
        margin-bottom: 30px;
    }

    .page-bp-coupon-detail,
    .page-bp-join,
    .page-user-manager,
    .page-bp-reservation {
        padding: 40px 0;
    }

    .um-event-detail-right {
        padding: 24px;
    }

    #joinModal {
        .modal-body {
            h3 {
                margin-bottom: 16px;
            }

            .check-mail {
                margin-top: 16px;

                a {
                    margin-top: 10px;
                    display: inline-block;
                }
            }
        }
    }

    .form-group-quantity button {
        height: 44px;
        width: 44px;
        min-width: 44px;
        line-height: 44px;
        font-size: $fs20;
        text-align: center;
        display: flex;
        align-items: center;
        justify-self: center;

        img {
            display: block;
            margin: 0 auto;
            max-width: 16px;
            transform: translateX(-8px);
        }
    }

    .bp-reservation-book {
        .container {
            margin-top: 60px;
            background: $bg-secondary;
            padding: 48px 24px;

            h3 {
                margin-bottom: 24px;
            }
        }

        .book-btn {
            margin-top: 24px;
        }
    }

    #aboutMapModal .modal-body {
        padding: 0 !important;
    }

    #reservationModal .modal-dialog .wrapper-btn {
        margin: 30px 0;

        .btn-ok {
            margin-right: 10px;
            width: 50px;
        }
    }

    .home-about-us {
        .content {
            padding: 40px 0;
        }

        h2 {
            height: 80px;
        }
    }

    #carouselCustomer .carousel-indicators [data-bs-target],
    .home-event-slider .carousel-indicators [data-bs-target] {
        height: 5px;
        width: 24px;
    }

    .home-coupon .customer-coupon-item,
    .home-service .customer-service-item {
        margin-bottom: 20px;
    }
    .page-footer {
        background: $accent-primary;
        padding: 40px 0;
        color: $white;

        .list-social {
            margin-bottom: 24px;

            li {
                &:not(:last-child) {
                    margin-right: 24px;
                }
            }
        }

        .list-links {
            li {
                &:not(:last-child) {
                    margin-right: 24px;
                }
            }
        }

        .img-paypal {
            margin-bottom: 30px;
        }

        &-left {
            border-right: 0;
        }

        &-right {
            text-align: center;

            h3 {
                margin-bottom: 20px;
            }
        }

        &-info {
            text-align: center;

            .footer-logo {
                width: 50%;
            }

            ul {
                margin-bottom: 16px;
            }

            li {
                display: block;

                &:not(:first-child) {
                    margin-top: 20px;
                }

                img {
                    margin-right: 6px;
                }
            }

        }
    }

    .tab-photo-wrap .photo-item {
        margin-bottom: 0;
    }

    .dropdown-country {
        height: 44px;

        .dropdown-menu {
            width: 100%;
        }

        .dropdown-toggle {
            line-height: 44px;

            &::after {
                top: 20px;
            }
        }
    }


    .form-group-datepicker {
        .form-control {
            padding-left: 50px;
        }

        .datepicker-wraper {
            .icon-calendar {
                position: absolute;
                z-index: 2;
                top: 10px;
                max-height: 24px;
            }
        }
    }

    .form-group-timepicker {
        .timepicker-wraper {
            &::after {
                position: absolute;
                right: 12px;
                top: 18px;
                z-index: 2;
                content: '';
                background-image: url('../images/ic-dropdown.svg');
                background-repeat: no-repeat;
                background-position: center;
                width: 16px;
                height: 8px;
            }
        }
    }

    .home-service {
        padding: 30px 0;
    }

    .customer-contact-left {
        padding: 24px;
    }


    #carouselCustomer img,
    .home-event-slider img {
        min-height: 250px;
        object-fit: cover;
    }

    #carouselCustomer  {
        .carousel-caption {
            bottom: 30px;
            margin-left: -40px;
        }

        h5 {
            font-size: 30px;
            line-height: 32px;
        }
    }

    .customer-service-item .card-title {
        font-size: 24px;
        line-height: 28px;
    }


    .customer-contact-left h3, .customer-contact-left .h3 {
        font-size: 40px;
        line-height: 80px;
    }
    .home-event-slider {
        .carousel-caption {
            bottom: 40px;
            margin-left: -40px;

            h4 {
                margin-bottom: 24px;
                padding: 16px;
                font-size: 30px;
            }
        }
    } 

    .customer-location-right {
        iframe {
            min-height: 300px;
        }
    }

    .nav-link {
        display: inline-block;
    }

    .wrapper-notify {
        right: unset;
        left: 0;
    }

    .navbar {
        border-bottom: 0;
        padding: 0;

        .navbar-nav {
            margin-bottom: 16px;
        }

        .nav-link {
            padding-left: 0 !important;
        }
    }

    .navbar-expand .navbar-nav,
    .navbar-expand .navbar-collapse {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
    }

    .owl-carousel .owl-nav .owl-prev {
        left: -5px;
    }

    .owl-carousel .owl-nav .owl-next {
        right: -5px;
    }
}

@media screen and (max-width: 576px) {
    .grid-60 [class*='col-'] {
        padding-left: 8px;
        padding-right: 8px;
    }

    .search-box {
        .form-control {
            padding: 7px 34px;
            font-size: 18px;
            line-height: 20px;
        }

        &-icon {
            left: 8px;
            top: 7px;

            img {
                max-height: 19px;
            }
        }
    }

    .pagination-left {
        margin: 16px 0;
    }

    .page-item {
        .page-link {
            font-size: $fs18;
            line-height: 20px;
        }
    }

    .signin-right {
        margin: 50px 0;
    }

    .page-notification {
        padding: 40px 0 60px;
    }

    .mb-100 {
        margin-bottom: 30px;
    }

    .page-signup {
        margin: 50px 0;
    }

    .search-box {
        width: 100%;
    }

    .page-notification {
        padding: 40px 0;
    }

    .notification {
        &-content {
            padding: 16px;
        }

        &-item {
            padding: 8px 16px;
        }

        &-sort {
            img {
                max-width: 24px;
            }

            .custom-select {
                margin-top: 8px;
                max-width: 120px;
            }
        }
    }

    .customer-event {
        padding: 40px 0;

        &-top {
            height: 52px;
        }

        &-left {
            .page-heading {
                margin-bottom: 16px;
            }
        }

        &-list {
            padding: 16px 0;
        }

        .zero-event {
            height: 300px;
            padding-top: 50px;
        }
    }

    .zero-box {
        height: 300px;
        padding: 50px 20px 20px;

        img {
            max-width: 100px;
        }

        p {
            margin-top: 30px;
        }
    }

    .bp-reservation {
        &-zero {
            height: 400px;
        }
    }

    .customer-event-item {
        padding: 16px;

        .event-img {
            margin-bottom: 16px;
        }

        .event-join {
            position: relative;
            right: 0;
            bottom: 0;
        }
    }

    .customer-service {
        &-content {
            h2 {
                margin-bottom: 30px;
            }
        }

        &-list {
            padding: 40px 0;

            .service-list-top {
                margin-bottom: 24px;
            }

            .zero-service {
                background-color: $bg-secondary;
                padding: 20px;
                text-align: center;
                height: 300px;

                .wrapper-searchbox+img {
                    padding-top: 0;
                }
            }
        }
    }

    .customer-item-list {
        padding: 16px;
        margin-bottom: 24px;

        .list-img {
            margin-bottom: 16px;
        }

        .wrapper-img {
            min-width: 16px;
            margin-right: 8px;

            img {
                max-width: 16px;
            }
        }

        .view-more {
            position: relative;
            right: 0;
            bottom: 0;

            .btn {
                margin-top: 10px;
            }
        }
    }

    .list-tags {
        margin-bottom: 16px;

        li {
            margin-bottom: 10px;
        }
    }

    .customer-coupon {
        padding: 40px 0;

        .search-box {
            margin: 16px 0;
        }

        &-item {
            margin-bottom: 16px;
        }
    }

    .bp-breadcrumb {
        font-size: 20px;
        line-height: 24px;
    }

    .bp-tabs-right {
        padding: 0;
        margin-top: 0;
    }

    .bp {
        &-coupon {
            &-detail {
                .detail-right {
                    .btn-getnow {
                        margin-top: 32px;
                    }
                }

                .description {
                    margin-top: 16px;
                    border: 2px solid $accent-primary;
                    padding: 16px 24px;
                }
            }
        }
    }
}